<template>
	<div class="rule">
		<div class="title">活动规则</div>
		<ul class="explain">
			<li class="mag">1、选择你的性别，根据提示选择你喜欢 的物品完成后邀请TA参与测试</li>
     		<li class="msg">2、对方接受邀请后，在测试题中找出您喜欢的物品即完成测试，获得双方的默契值。</li>
      		<li class="msg">3、本次活动可邀请多人与您进行默契值测试。</li>
      		<li class="msg msg-bottom">4、本次活动时间：即日起至2018年2月14日。</li>
      		<li class="msg">5、玫瑰数量有限，送完即止。</li>
      		<li class="msg">6、本次活动时间：即日起至2018年2月14日。</li>
		</ul>
		<div class="answer" @click="goHome">返回</div>
	</div>	
</template>
<script>
	export default{
		nane:'rule',
		data(){
			return{

			}
		},
		methods: {
			goHome(){
				this.$router.go(-1)
			}
		}
	}
</script>
<style scoped lang="less">
@import "./../style/mixin.less";
	.rule{
		position: absolute;
		top: 0;
		left: 0;
		width: 750*@rem;height: 1208*@rem;
		background: url(../assets/rule-bg.png) no-repeat;
		background-size: 100% 100%;
		color: #ff278d;
		font-weight: bold;
		.title{ font-size: 50*@rem; margin-top: 90*@rem; text-align: center; margin-bottom: 40*@rem;}
		.explain{ margin: 0 57*@rem; font-size: 30*@rem;margin-bottom: 80*@rem;
			.msg{line-height: 60*@rem;}
		}
		.answer{ background: #ff57ac; width: 360*@rem; text-align: center; margin: 0 auto; font-weight: bold;  color: #FFFFFF;font-size: 40*@rem;height: 80*@rem; line-height:80*@rem;  border-radius: 20*@rem;}
	}
</style>



